@tailwind base;
@tailwind components;
@tailwind utilities;

.fade-in {
  opacity: 0;
}

.fade-in.active {
  opacity: 1;
  transition: opacity 4s ease-in-out;
}

.fade-in-hero.active {
  opacity: 1;
  transition: opacity 2s ease-in-out;
}

.nav-link{
  color: rgba(255,255,255,0.75);
  position: relative;
  transition: all 0.4s;
}

.nav-link:hover{
  color:#fff;
}

.nav-link::after{
  content:'';
  height:2px;
  width:100%;
  background:rgb(212,140,56);
  display:block;
  position:absolute;
  left:0;
  bottom:-8px; 
  opacity: 0;
  transition: all 0.2s;
  pointer-events: none;
}

.nav-link:hover::after{
  opacity:1;
}

